<template>
  <div>
    <navMedic/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Registro del Médico</b>
      </h1>
      <h5 class="text-center text-dark"><b>Llene el siguiente formulario</b></h5>
      <div style="width:400px">
        <b-alert show variant="danger" style="width:400px" v-if="isCorrect!==null">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
      </div>
      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-4">
            <label for="tipoDocumento">Tipo de Documento</label>
            <select
              class="form-control"
              id="tipoDocumento"
              v-model.number="tipoDocumento.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Pasaporte</option>
              <option value="2">Cedula</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="numDocumento">Numero de Documento</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              v-model="numDocumento"
              maxlength="10"
              type="text"
              required
            >
          </div>
          <div class="col-md-4">
            <label for="numDocumento">Numero de Matricula</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              v-model="numMatricula"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="nombre">Nombres</label>
            <input
              class="form-control"
              placeholder="Ingrese nombres"
              v-model="nombre"
              type="text"
              required
            >
          </div>
          <div class="col-md-6 mb-3">
            <label for="nombre">Apellidos</label>
            <input
              class="form-control"
              placeholder="Ingrese apellidos"
              v-model="apellido"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="numTelefono">Numero Telefono</label>
            <input
              class="form-control"
              placeholder="Numero de telefono"
              v-model="numTelefono"
              type="text"
              maxlength="10"
            >
          </div>
          <div class="col-md-6">
            <label for="numTelefono">Numero Celular</label>
            <input
              class="form-control"
              placeholder="Numero de celular"
              v-model="numCelular"
              maxlength="10"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-12">
            <label for="especialidad">Especialidad</label>
            <input
              class="form-control"
              placeholder="Especialidad"
              v-model="especialidad"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <button class="btn btn-primary">Registrar</button>
        </div>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navMedic from "@/components/medic/navMedic.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "nuevoMedico",
  data() {
    return {
      tipoDocumento: {
        id: null
      },
      numMatricula: null,
      apellido: null,
      nombre: null,
      numDocumento: null,
      especialidad: null,
      numTelefono: null,
      numCelular: null,
      message: null,
      avise: null,
      isCorrect: null
    };
  },
  methods: {
    save(e) {
      e.preventDefault();
      this.avise = "";
      this.message = "";
      axios
        .post(`${URL}/api/medico`, {
          tipoDocumento: this.tipoDocumento,
          numMatricula: this.numMatricula,
          apellido: this.apellido,
          nombre: this.nombre,
          numDocumento: this.numDocumento,
          especialidad: this.especialidad,
          numTelefono: this.numTelefono,
          numCelular: this.numCelular
        })
        .then(response => {
          this.tipoDocumento = {};
          this.numMatricula = null;
          this.apellido = null;
          this.nombre = null;
          this.numDocumento = null;
          this.especialidad = null;
          this.numTelefono = null;
          this.numCelular = null;
          bootbox.alert({
            title: "Datos Guardados",
            message: response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/medicos")
          });
        })
        .catch(error => {
          this.avise = "Error";
          this.isCorrect = false;
          this.message = "Detalle: " + error.response.data.mensaje;
          bootbox.alert({
            title: "Error",
            message: "No se pudo registrar",
            locale: "es",
            backdrop: true
          });
          console.log("Error " + error.response.status + " Conflict");
        });
    }
  },
  components: {
    navMedic,
    Footer
  }
};
</script>

